@import "../../global.scss";
.works {
  // background-color: lightsalmon;
  display: flex;
  align-items: center;
  justify-content: center;
  position:relative;

  .arrow{
    height: 50px;
    position: absolute;
    cursor: pointer;

     @include mobile {
      margin-top: 600px;
    }
    &.left{
      left: 100px;
      transform: rotate(180deg);
    }

    &.right{
      right: 100px;

    }
    
  }

  .slider{
    height: 350px;
    display: flex;
    position: absolute;
    left: 0;
    transition: all 1s ease-out;
    .container{
      width: 100vw;
      display: flex;
      align-items: center;
      justify-content: center;

      .item{
        width: 700px;
        height: 100%;
        background-color: white;
        border-radius: 20px;
        display: flex;
        align-items: center;
        justify-content: center;

        .left{
          flex: 4;
          height: 80%;
          display: flex;
          align-items: center;
          justify-content: center;

          .leftContainer{
            width: 90%;
            height: 85%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .imgContainer{
              width: 40px;
              height: 40px;
              border-radius: 50%;
              background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
              display: flex;
              align-items: center;
              justify-content: center;

              img{
                width: 25px;
              }
            }
            h2{
              font-size: 20px;
              color: black;
              overflow: hidden;

              @include mobile{
      font-size: 14px;
    }
            }
            p{
              font-size: 13px;
              color: black;
              overflow: hidden;

                  @include mobile{
      font-size: 11px;
    }
            }
            span{
              font-size: 12px;
              font-weight: 600;
              text-decoration: underline;
              color: black;
              cursor: pointer;
              overflow: hidden;
            }
          }
        }
        .right{
          flex: 8;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          img{
            transform: rotate(5deg);
          }
        }

      }
    }
  }
}